.van-loading {
  width: 30px;
  height: 30px;
  z-index: 0;
  font-size: 0;
  line-height: 0;
  position: relative;
  vertical-align: middle
}

.van-loading--circle {
  width: 16px;
  height: 16px
}

.van-loading__spinner {
  z-index: -1;
  width: 100%;
  height: 100%;
  position: relative;
  display: inline-block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-animation: van-rotate .8s linear infinite;
  animation: van-rotate .8s linear infinite
}

.van-loading__spinner--circle {
  border-radius: 100%;
  border: 3px solid transparent
}

.van-loading__spinner--gradient-circle {
  background-size: contain
}

.van-loading__spinner--spinner {
  -webkit-animation-timing-function: steps(12);
  animation-timing-function: steps(12)
}

.van-loading__spinner--spinner i {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute
}

.van-loading__spinner--spinner i::before {
  width: 2px;
  height: 25%;
  content: ' ';
  display: block;
  margin: 0 auto;
  border-radius: 40%;
  background-color: currentColor
}

.van-loading__spinner--circular {
  -webkit-animation-duration: 2s;
  animation-duration: 2s
}

.van-loading__circular {
  width: 100%;
  height: 100%
}

.van-loading__circular circle {
  stroke-width: 3;
  stroke-linecap: round;
  -webkit-animation: van-circular 1.5s ease-in-out infinite;
  animation: van-circular 1.5s ease-in-out infinite
}

.van-loading--black .van-loading__spinner--circle {
  border-color: #c9c9c9;
  border-top-color: #666
}

.van-loading--black .van-loading__spinner--gradient-circle {
  background-image: url(https://img.yzcdn.cn/vant/gradient-circle-black.png)
}

.van-loading--black .van-loading__spinner--spinner {
  color: #c9c9c9
}

.van-loading--black circle {
